@import "@/scss/settings.scss";










































































.left{
    position: fixed;
    width: 50vw;
    right:0;
    top:0;
    height: 100vh;
    justify-content: center;
    align-items: center;
    display: none;
    pointer-events: none;
    @media screen and (min-width : $medium){
        display: flex;
    } 
}

.content{
    padding: 0 10%;
    &  img{
        margin: 20px 0;
    }
}

