@import "@/scss/settings.scss";



















































.accordion-container{
    padding:10%;
    font-size: 20px;
}
.section{
    display:flex;
    flex-direction: column;
    .top{
        z-index:99;
        top: 0px;
        padding: 0 10px;
        position: sticky;
        display:flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: row;
        height: 50px;
        box-shadow: 0 0 0 2px black;
        background: #E2E4E6;
        font-weight: 600;
    }
    .open svg{
        transform: rotate(.25turn);
    }
    svg{
        transition: all .2s ease;
        width:30px;
        height:30px;
    }
    .content{
        max-height: 600px;
        overflow:scroll;
         padding: 30px 20px;
    }
}

